import React from "react";

const ICONS = {
  INVALID: "",
  "OTHER POSITION": "",
  FOUND: "",
  "MEI MATCH": "",
  "UYIR MATCH": "",
};

function Success({ words, attempts }) {
  let guesses = [];

  const triggerShare = () => {
    let text = `${words.length}      ${attempts}  .\n\n     .`;
    if (navigator.share) {
      navigator.share({ text, url: window.location.href });
    } else {
      navigator.clipboard.writeText(text + "\n" + window.location.href);
      alert("Content copied to clipboard");
    }
  };

  return (
    <div className="card" style={{ marginTop: "30vh" }}>
      <header className="card-header">
        <p className="card-header-title">
          <span className="has-text-centered mx-auto">
             ! 
          </span>
        </p>
      </header>
      <div className="card-content">
        <p className="has-text-centered my-6">
           <span className="tag is-dark">{words.length}</span>{" "}
           <span className="tag is-dark">{attempts}</span>{" "}
            .
        </p>
        <div className="is-flex is-flex-direction-column">
          {guesses.map(({ results }, i) => (
            <div key={i}>
              {results.reduce((acc, curr) => acc + ICONS[curr], "")}
            </div>
          ))}
        </div>
      </div>
      <footer className="card-footer">
        <button
          href="#"
          className="card-footer-item is-ghost"
          onClick={() => triggerShare()}
        >
           
        </button>
      </footer>
    </div>
  );
}

export default Success;
