import React, { useState } from "react";
import { b64EncodeUnicode } from "../utils";

function WordSetter() {
  const [device, setDevice] = useState("");
  const [words, setWords] = useState([]);

  const startPlaying = () => {
    let hash = encodeURIComponent(b64EncodeUnicode(words.join(",")));
    if (hash.length > 1024) {
      alert(
        "      .    ."
      );
      return;
    }
    if (device === "this") {
      window.location = `/sollodu/?q=${hash}`;
    } else {
      let url = `${window.location.origin}/sollodu/?q=${hash}`;

      if (navigator.share) {
        navigator.share({ url });
      } else {
        navigator.clipboard.writeText(url);
        alert(
          "  ' '  . ctrl+v  +v      ."
        );
      }
    }
  };

  const handleTextChange = (e) => {
    console.log(e.target.value);
    let items = e.target.value.trim().split("\n");
    setWords(items.filter((i) => i.length > 1).map((i) => i.trim()));
  };

  return (
    <div>
      <h1 className="h1 has-text-centered py-5 has-text-weight-bold">
        
      </h1>
      {!device ? (
        <div className="section has-text-centered">
          <h3 className="h3 mb-6">   ?</h3>
          <div className="buttons is-justify-content-center">
            <button
              className="button is-primary"
              onClick={() => setDevice("this")}
            >
               
            </button>
            <button
              className="button is-link"
              onClick={() => setDevice("others")}
            >
               
            </button>
          </div>
        </div>
      ) : (
        <div className="section has-text-centered">
          <p>     </p>
          <textarea
            className="textarea my-5"
            name="words"
            id="words"
            rows="5"
            onChange={handleTextChange}
          ></textarea>

          {device !== "this" ? (
            <p className="my-3">
                   
            </p>
          ) : null}

          <button className="button is-primary" onClick={startPlaying}>
            {device === "this" ? " " : " "}
          </button>
        </div>
      )}
    </div>
  );
}

export default WordSetter;
